import { FdfDocument, TOCDocument } from "../../../game/ui/fdf";


export default {
  extensions: [['.toc', 'text']],
  fdfHandler: {
    extensions: [['.fdf', 'text']],
    Constructor: FdfDocument
  },
  install(viewer:any) {
    viewer.addHandler(this.fdfHandler);
    return true;
  },
  Constructor: TOCDocument,
  resolveToc(path: string) {
    return [path, '.toc', true]
  }
};
