import React from 'react';
import { Route } from 'react-router-dom';
import { Nav, NavItem, NavDropdown, NavLink } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import './DataMenu.scss'

import AppCache from './data/cache';
import objectTypes from './objects/types';

const ObjectMenuInner = ({ match: { params: { build, type } } }) => (
  <NavDropdown title={objectTypes[type] || "Objects"} active={objectTypes[type] != null} id="objects-menu">
    {Object.keys(objectTypes).map(t => (
      <NavDropdown.Item key={t} href={`/${build}/${t}`}>
        <LinkContainer to={`/${build}/${t}`}>
          <NavItem event-key={`objects.${t}`}>
            <span className={`ObjectIcon ${t}`} />{objectTypes[t]}
          </NavItem>
        </LinkContainer>
      </NavDropdown.Item>
    ))}
  </NavDropdown>
);

const ObjectMenu = () => <Route path="/:build/:type?" component={ObjectMenuInner} />;

