const DataMenu2 = () => (
  <AppCache.DataContext.Consumer>
    {data => (
      <React.Fragment>
        <ul className='DataMenu'>
          <LinkContainer className='title' to={`/${data.id}`} exact>
            <li><NavLink event-key="build">{data.name}</NavLink></li>
          </LinkContainer>
          <li>
            <LinkContainer to={`/${data.id}/files`}>
              <NavLink event-key="files">Files</NavLink>
            </LinkContainer>
          </li>
          {(!data.isMap || data.hasFile("objects.json")) &&
            <React.Fragment>
              <li className='sep'> | </li>
              <li > <ObjectMenu /></li>
            </React.Fragment>
          }
          {(!!data.isMap) && (
            <React.Fragment>
              <li className='sep'> | </li>
              <li> <LinkContainer to={`/${data.id}/map`}>
                <NavLink event-key="map">Map</NavLink>
              </LinkContainer>
              </li>
            </React.Fragment>
          )}
          {(data.hasFile("war3map.j") || data.hasFile("Scripts\\war3map.j")) && (
            <React.Fragment>
              <li className='sep'> | </li>
              <li> <LinkContainer to={`/${data.id}/script`}>
                <NavLink event-key="script">Script</NavLink>
              </LinkContainer>
              </li>
            </React.Fragment>
          )}
        </ul>
      </React.Fragment>
    )}
  </AppCache.DataContext.Consumer >
);

export default DataMenu2;
