const { createToken, Lexer } = require('chevrotain')

const tokens = [
  createToken({
    name: 'comment',
    pattern: /\/\/.*/,
    group: Lexer.SKIPPED
  }),
  createToken({
    name: 'whitespace',
    pattern: /\s+/,
    group: Lexer.SKIPPED
  }),
  createToken({ name: 'RAWCODE', pattern: /('''.*?''')/ }),
  createToken({ name: 'COMMA', pattern: /,/ }),
  createToken({ name: 'IF', pattern: /if/ }),
  createToken({ name: 'THEN', pattern: /then/ }),
  createToken({ name: 'ELSEIF', pattern: /elseif/ }),
  createToken({ name: 'ELSE', pattern: /else/ }),
  createToken({ name: 'ENDIF', pattern: /endif/ }),
  createToken({ name: 'GLOBALS', pattern: /globals/ }),
  createToken({ name: 'ENDGLOBALS', pattern: /endglobals/ }),
  createToken({ name: 'FUNCTION', pattern: /function/ }),
  createToken({ name: 'TAKES', pattern: /takes/ }),
  createToken({ name: 'TYPE', pattern: /type/ }),
  createToken({ name: 'ARRAY', pattern: /array/ }),
  createToken({ name: 'NULL', pattern: /null/ }),
  createToken({ name: 'TRUE', pattern: /true/ }),
  createToken({ name: 'FALSE', pattern: /false/ }),
  createToken({ name: 'CONSTANT', pattern: /constant/ }),
  createToken({ name: 'HEX_CONSTANT', pattern: /0x(([0-9]|[a-f]|[A-F])*)/ }),
  createToken({ name: 'DOLLAR_HEX_CONSTANT', pattern: /\$(([0-9]|[A-F])*)/ }),
  createToken({ name: 'RETURNS', pattern: /returns/ }),
  createToken({ name: 'RETURN', pattern: /return/ }),
  createToken({ name: 'EXTENDS', pattern: /extends/ }),
  createToken({ name: 'NATIVE', pattern: /native/ }),
  createToken({ name: 'ENDFUNCTION', pattern: /endfunction/ }),
  createToken({ name: 'AND', pattern: /and(?=[\s()])/ }),
  createToken({ name: 'OR', pattern: /or(?=[\s()])/ }),
  createToken({ name: 'LOCAL', pattern: /local/ }),
  createToken({ name: 'SET', pattern: /set(?=\s)/ }),
  createToken({ name: 'LOOP', pattern: /loop(?=\s)/ }),
  createToken({ name: 'EXITWHEN', pattern: /exitwhen/ }),
  createToken({ name: 'ENDLOOP', pattern: /endloop/ }),
  createToken({ name: 'NOTHING', pattern: /nothing/ }),
  createToken({ name: 'NOT', pattern: /not/ }),
  createToken({ name: 'CALL', pattern: /call(?=\s)/ }),
  createToken({ name: 'MULT', pattern: /\*/ }),
  createToken({ name: 'DIV', pattern: /\// }),
  createToken({ name: 'PLUS', pattern: /\+/ }),
  createToken({ name: 'MINUS', pattern: /-/ }),
  createToken({ name: 'ID', pattern: /[a-zA-Z]\w*/ }),
  createToken({ name: 'LPAREN', pattern: /\(/ }),
  createToken({ name: 'RPAREN', pattern: /\)/ }),
  createToken({ name: 'LSQUAREPAREN', pattern: /\[/ }),
  createToken({ name: 'RSQUAREPAREN', pattern: /\]/ }),
  createToken({ name: 'NOTEQUALS', pattern: /!=/ }),
  createToken({ name: 'EQUALSEQUALS', pattern: /==/ }),
  createToken({ name: 'LESSOREQUALS', pattern: /<=/ }),
  createToken({ name: 'GREATEROREQUALS', pattern: />=/ }),
  createToken({ name: 'EQUALS', pattern: /=/ }),
  createToken({ name: 'LESS', pattern: /</ }),
  createToken({ name: 'GREATER', pattern: />/ }),
  createToken({ name: 'IDLITERAL', pattern: /'.*?'/ }),
  createToken({ name: 'STRINGLITERAL', pattern: /"[\s\S]*?"/, line_breaks: true }),
  createToken({ name: 'REAL', pattern: /[0-9]+\.[0-9]+/ }),
  createToken({ name: 'INTEGER', pattern: /[0-9]+/ }),
  createToken({ name: 'NL', pattern: /[\r\n]+/ }),
]

const tokenVocabulary = {}

tokens.forEach(tokenType => {
  tokenVocabulary[tokenType.name] = tokenType
})
const JassLexer = new Lexer(tokens)

module.exports = {
  JassLexer,
  tokenVocabulary
}
