import { parse } from "./JassParser";
import { JassVisitor, jassAstNode } from "./JassVisitor";
import * as constants from './gameConstants';
import { jassNativeFunctions } from "./gameFunctions";
const visit = function (runtime, inputstr, option) {
    const visitor = new JassVisitor(runtime, option.jassfile, option.api);
    const rule = option.rule || 'program';
    const parsed = parse(inputstr, rule, option.debugParser);
    const p = visitor[rule](rule == "program" ? parsed : parsed.children, []);
    return p.jass2js(visitor);
}
export class JassRuntime {
    fileSymbols = [];
    unresolved = [];
    enums = {};
    natives;
    gameTypes = [];
    constants = {};
    scripts = {}
    converted = []
    app;
    triggerData;
    constructor(app) {
        this.gameTypes = constants.gameTypes;
        this.constants = constants;
        this.natives = new jassNativeFunctions(app);
        this.app = app;
        this.triggerData = app.triggerData;
    }
    load(jassCode, fileName, parseTarget = "program") {
        fileName = "GAMESCRIPT_" + fileName.replaceAll(/[./\\]/g, "_");
        let jsCode = visit(this, jassCode, {
            jassfile: fileName,
            rule: parseTarget
        });
        const blob = new Blob([jsCode], { type: 'text/javascript' });
        const blobUrl = URL.createObjectURL(blob);
        const script = document.createElement("script");
        script.type = blob.type;
        script.async = true;
        script.src = blobUrl;
        script.id = fileName;
        document.head.appendChild(script);
        this.scripts[fileName] = script;
        let runtime = this;
        return new Promise((resolve, reject) => {
            script.onload = () => {
                try {
                    /* eslint-disable no-undef */
                    if (parseTarget == 'program')
                        globalThis[fileName].call(runtime);
                    resolve();
                } catch (e) {
                    reject(e);
                }
            }
        });
    }
    loadStart(fileName) {
        console.log('loadStart', fileName)
    }
    loaded(fileName) {
        console.log('loaded', fileName);
    }
    // define enum
    enum(v, name, type) {
        if (!this.enums[type]) {
            this.enums[type] = this.enums[type] || {};
            this.enums[type][v] = name;
            this.enums[type][name] = v;
            this.enums['convert' + type.toLowerCase()] = function (n) {
                return this.enums[type][n];
            }
        };
        return v;
    }
    call(fn, args = []) {
        // functions that defined using this.xxx = function(){} in blizard.j or common.j
        if (this[fn] instanceof Function) {
            if (this.natives[fn] instanceof Function) {
                console.warn('W', 'duplicate implementation of', fn)
            }
            return this[fn](...args);
        }
        // functions that define enum like constant, in generated js code, we converted it value to an array
        // that array contains 3 elements includes enumType enumName enumInt
        if (fn.startsWith('Convert') && args.length == 1) {
            if (this.gameTypes.includes(fn.substring(7).toLowerCase()))
                return this.directConvert(args[0]);
            let items = this.triggerData.triggerParams
                .filter(x => x.variableType == fn.substring(7).toLowerCase());
            if (items.length) {
                // items.forEach((x, i) => this.constants[x.key] = i);
                return this.directConvert(args[0]);
            }
        }
        if (this.natives[fn] instanceof Function) {
            let d = this.natives[fn](...args);
            if (this.natives.logCalls.contains(fn)) {
                console.log('[------LOG------]', fn, args || [], ', returns', d === undefined ? 'nothing' : d);
            }
            else {
                console.log('[LOG]', fn, args || [], ', returns', d === undefined ? 'nothing' : [d]);
            }
            return d;
        }
        return console.info('[NotDeclared]', fn, args || []);
    }
    directConvert(n) {
        return n;
    }
    async config() {
        debugger;
    }
    async main() {
        debugger;
    }
    // native
    checkNative(fn) {
        return true;
    }
    resolveFunctions(names = []) {
        const unresolved = names.filter(x => !this.natives[x]);
        if (unresolved.length) {
            throw new Error('assert: unresolved functions:');
        }
        let code = names.map(x => x + '(){\ndebugger\n}').join('\n')
    }
    resolveVars(names = []) {
        const unresolved = names.filter(x => this.constants[x] === undefined);
        if (unresolved.length) {
            console.log("assert: unresolved vars:", unresolved);
            // throw new Error('assert: unresolved vars:');
        }
        let code = unresolved.map(x => 'this.' + x + '=null;').join('\n')
    }
    promised = ["CreateUnit", "Preloader", "Filter"];
    isPromisedFunction(fn) {
        if (this.promised.includes(fn)) {
            return true;
        }
        else if (this.natives[fn] == undefined) {
            return true;
        }
        else {
            return false;
        }
    }
    async call2(fn, args = []) {
        let d = this.call(fn, args);
        if (d instanceof Promise) {
            await d;
        }
        return d;
    }
}
