import { JassRuntime } from "../../../game/jass2js/JassRuntime";
import Resource from "../resource";

class JassDocument extends Resource {
    vm: any;
    constructor(d: { viewer:any, handler:any, extension:any, pathSolver:any, fetchUrl:string, path:string }) {
        super(d);
        this.vm = this.viewer.vm || (this.viewer.vm = new JassRuntime(this.viewer));
    }
    async load(src: any): Promise<void> {
        await this.viewer.vm.load(src, this.path);
        if (this.path.endsWith('war3map.j')) {
            console.log('jassvm', "Enter vm::config.")
            await this.vm.config()
            console.log('jassvm', "Exit vm::config.")
            console.log('jassvm', "Enter vm::main.")
            await this.vm.main();
            console.log('jassvm', "Exit vm::main.")
        }
    }
}
export default {
    extensions: [['.j', 'text']],
    Constructor: JassDocument
};
