// deno-lint-ignore-file no-unused-vars

import { Adapter } from "./adapter";
import {
  FRAMEPOINT_BOTTOM,
  FRAMEPOINT_BOTTOMLEFT,
  FRAMEPOINT_BOTTOMRIGHT,
  FRAMEPOINT_CENTER,
  FRAMEPOINT_LEFT,
  FRAMEPOINT_RIGHT,
  FRAMEPOINT_TOP,
  FRAMEPOINT_TOPLEFT,
  FRAMEPOINT_TOPRIGHT,
  originframetype,
  framehandle,
  framepointtype,
  RelativeFrameSide,
  textaligntype,
  frameeventtype,
  trigger,
  event,
  handle
} from "./customUiApi";
import { FdfArg, FdfFrame } from "./fdf";
const posRelatativeToWindow = () => {
  return {
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
    center: undefined,
  }
}
const posRelatativeToParent = (parent: framehandle) => {
  return {
    top: {
      relative: parent,
      relativeSide: "top",
      xOffset: 0,
      yOffset: 0,
    },
    bottom: {
      relative: parent,
      relativeSide: "bottom",
      xOffset: 0,
      yOffset: 0,
    },
    left: {
      relative: parent,
      relativeSide: "left",
      xOffset: 0,
      yOffset: 0,
    },
    right: {
      relative: parent,
      relativeSide: "right",
      xOffset: 0,
      yOffset: 0,
    },
    center: undefined,
  }
}
type ParseArg<T> = {
  (
    args: ReadonlyArray<FdfArg> | undefined,
    name: string,
    fn: (text: T) => void,
  ): void;
  (
    args: ReadonlyArray<FdfArg> | undefined,
    name: string,
    index: number,
    fn: (text: T) => void,
  ): void;
};

const parseString = ((
  args: ReadonlyArray<FdfArg> | undefined,
  name: string,
  index: number | ((text: string) => void),
  fn: undefined | ((text: string) => void),
) => {
  if (typeof index === "function") {
    fn = index;
    index = 0;
  }

  const value = args?.[index];
  if (typeof value === "string") fn!(value);
  else {
    console.warn(
      `Expected string for ${name}, got: ${JSON.stringify(value)
      } (index ${index}) in ${JSON.stringify(args)}`,
    );
  }
}) as ParseArg<string>;

const parseNumber = ((
  args: ReadonlyArray<FdfArg> | undefined,
  name: string,
  index: number | ((value: number) => void),
  fn: undefined | ((value: number) => void),
) => {
  if (typeof index === "function") {
    fn = index;
    index = 0;
  }

  const value = args?.[index];
  if (typeof value === "number") fn!(value);
  else {
    console.warn(
      `Expected number for ${name}, got: ${JSON.stringify(value)
      } (index ${index}) in ${JSON.stringify(args)}`,
    );
  }
}) as ParseArg<number>;
export class FrameLib {

  createFrame: (def, parent: framehandle | null) => framehandle;
  BlzCreateFrame: (name: string, owner: framehandle, priority: number, createContext: number) => framehandle;
  BlzCreateSimpleFrame: (name: string, owner: framehandle, createContext: number) => framehandle;
  BlzCreateFrameByType: (typeName: string, name: string, owner: framehandle, inherits: string, createContext: number) => framehandle;
  BlzDestroyFrame: (frame: framehandle) => void;
  BlzFrameSetPoint: (frame: framehandle, point: framepointtype, relative: framehandle, relativePoint: framepointtype, x: number, y: number) => void;
  BlzFrameSetAbsPoint: (frame: framehandle, point: framepointtype, x: number, y: number) => void;
  BlzFrameClearAllPoints: (frame: framehandle) => void;
  BlzFrameSetAllPoints: (frame: framehandle, relative: framehandle) => void;
  BlzFrameSetVisible: (frame: framehandle | null, visible: boolean) => void;
  BlzFrameIsVisible: (frame: framehandle) => boolean;
  BlzGetFrameByName: (name: string, createContext: number) => framehandle;
  BlzFrameGetName: (frame: framehandle) => string;
  BlzFrameClick: (frame: framehandle) => void;
  BlzFrameSetText: (frame: framehandle, text: string) => void;
  BlzFrameGetText: (frame: framehandle) => string | undefined;
  BlzFrameAddText: (frame: framehandle, text: string) => void;
  BlzFrameSetTextSizeLimit: (frame: framehandle, size: number) => void;
  BlzFrameGetTextSizeLimit: (frame: framehandle) => number;
  BlzFrameSetTextColor: (frame: framehandle, color: number) => void;
  BlzFrameSetFocus: (frame: framehandle, flag: boolean) => void;
  BlzFrameSetModel: (frame: framehandle, modelFile: string, cameraIndex: number) => void;
  BlzFrameSetEnable: (frame: framehandle, enabled: boolean) => void;
  BlzFrameGetEnable: (frame: framehandle) => boolean;
  BlzFrameSetAlpha: (frame: framehandle, alpha: number) => void;
  BlzFrameGetAlpha: (frame: framehandle) => number;
  BlzFrameSetSpriteAnimate: (frame: framehandle, primaryProp: number, flags: number) => void;
  BlzFrameSetTexture: (frame: framehandle, texFile: string, flag: number, blend: boolean) => void;
  BlzFrameSetScale: (frame: framehandle, scale: number) => void;
  BlzFrameSetTooltip: (frame: framehandle, tooltip: framehandle) => void;
  BlzFrameCageMouse: (frame: framehandle, enable: boolean) => void;
  BlzFrameSetValue: (frame: framehandle, value: number) => void;
  BlzFrameGetValue: (frame: framehandle) => number;
  BlzFrameSetMinMaxValue: (frame: framehandle, minValue: number, maxValue: number) => void;
  BlzFrameSetStepSize: (frame: framehandle, stepSize: number) => void;
  BlzFrameSetSize: (frame: framehandle, width: number, height: number) => void;
  BlzFrameSetVertexColor: (frame: framehandle, color: number) => void;
  BlzFrameSetLevel: (frame: framehandle, level: number) => void;
  BlzFrameSetParent: (frame: framehandle, parent: framehandle) => void;
  BlzFrameGetParent: (frame: framehandle) => framehandle | null;
  BlzFrameGetHeight: (frame: framehandle) => number;
  BlzFrameGetWidth: (frame: framehandle) => number;
  BlzFrameSetFont: (frame: framehandle, fileName: string, height: number, flags: number) => void;
  BlzFrameSetTextAlignment: (frame: framehandle, vert: textaligntype, horz: textaligntype) => void;
  BlzTriggerRegisterFrameEvent: (whichTrigger: trigger, frame: framehandle, eventId: frameeventtype) => event;
  BlzGetTriggerFrame: () => framehandle;
  BlzGetTriggerFrameEvent: () => frameeventtype;
  BlzGetTriggerFrameValue: () => number;
  BlzGetTriggerFrameText: () => string;
  BlzFrameGetChildrenCount: (frame: framehandle) => number;
  BlzFrameGetChild: (frame: framehandle, index: number) => framehandle;
  frameDefs: {};
  BlzGetOriginFrame: (frameType: originframetype, index: number) => framehandle;
  BlzEnableUIAutoPosition: (enable: boolean) => void;
  BlzHideOriginFrames: (enable: boolean) => void;
  BlzConvertColor: (a: number, r: number, g: number, b: number) => number;
  idNext = 10000;
  constructor(frameDefs, adapter: Adapter) {
    this.frameDefs = frameDefs;
    this.BlzGetOriginFrame = (
      frameType: originframetype,
      index: number,
    ): framehandle =>
      adapter.selectAll(".origin-" + frameType.originframetypeId)[index];

    const newFrame = (def, id, parent: framehandle | null) => {
      const isRelativeToRoot = def.setAllPoints || !parent;
      return <framehandle>{
        handleId: id,
        framehandleId: id,
        parent: parent,
        def: def,
        node: null,
        width: 0,
        height: 0,
        pos: isRelativeToRoot ? posRelatativeToWindow() : posRelatativeToParent(parent),
        children: [],
        visible: true,
        scale: 1,
      }
    }
    this.BlzEnableUIAutoPosition = (enable: boolean): void => { };

    this.BlzHideOriginFrames = (enable: boolean): void => { };

    this.BlzConvertColor = (
      a: number,
      r: number,
      g: number,
      b: number,
    ): number => a * 255 ** 3 + r * 255 ** 2 + g * 255 + b;
    this.createFrame = (def, parent) => {
      const id = this.idNext++;
      const fh: framehandle = newFrame(def, id, parent);
      // owner may be missing on root frame
      parent?.children.push(fh);
      const inherits: FdfFrame | undefined =
        this.frameDefs[def.inherits ?? def.name];
      let decorateFileNames = false;
      const functionalChildren = new Set<string>();
      if (inherits) {
        for (const property of (inherits.properties ?? [])) {
          const args = property.args;
          switch (property.name) {
            case 'SetAllPoints': // ?
              fh.pos = {
                left: 0,
                right: 0,
                bottom: 0,
                top: 0,
                center: undefined,
              };
              break;
            case "DecorateFileNames":
              decorateFileNames = true;
              break;
            case "ButtonText":
              parseString(
                args,
                "ButtonText",
                (name) => functionalChildren.add(name),
              );
              break;
            case "Text":
              parseString(args, "Text", (text) => fh.text = text);
              break;
            case "Width":
              parseNumber(args, "Width", (width) => fh.width = width);
              break;
            case "Height":
              parseNumber(args, "Height", (height) => fh.height = height);
              break;
            case "ControlStyle":
              parseString(
                args,
                "ControlStyle",
                (styles) => fh.controlStyles = styles.split("|"),
              );
              break;
            case "ButtonPushedTextOffset":
              // IDK
              break;
            case "FrameFont":
              parseNumber(args, "FrameFont", 1, (size) => fh.fontSize = size);
              break;
            default:
              console.warn(`Unhandled fdf property ${property.name}`);
          }
        }
      }

      fh.node = adapter.createNode(fh, parent);

      if (inherits) {
        for (const child of (inherits.children ?? [])) {
          this.createFrame(child, fh);
        }
      }

      return fh;
    };

    this.BlzCreateFrame = (
      name: string,
      owner: framehandle,
      priority: number,
      createContext: number,
    ): framehandle => {
      let def = this.frameDefs[name];
      if (def == null)
        throw new Error('FrameNotFound');
      return this.createFrame(def, owner);
    }

    this.BlzCreateSimpleFrame = (
      name: string,
      owner: framehandle,
      createContext: number,
    ): framehandle => {
      let def = this.frameDefs[name];
      if (def == null)
        throw new Error('FrameNotFound');
      return this.createFrame(def, owner);
    }

    this.BlzCreateFrameByType = (
      typeName: string,
      name: string,
      owner: framehandle,
      inherits: string,
      createContext: number,
    ): framehandle => {
      let def = this.frameDefs[name];
      if (def == null)
        throw new Error('FrameNotFound');
      return this.createFrame(def, owner);
    };

    this.BlzDestroyFrame = (frame: framehandle): void => {
      adapter.remove(frame);
    };


    this.BlzFrameSetPoint = (
      frame: framehandle,
      point: framepointtype,
      relative: framehandle,
      relativePoint: framepointtype,
      x: number,
      y: number,
    ): void => {
      const relativeSide = pointMap.get(relativePoint);
      if (!relativeSide) {
        return console.warn(
          "Unknown relativePoint passed to BlzFrameSetPoint: " +
          relativePoint.framepointtypeId,
        );
      }

      if (leftPoints.includes(point)) {
        frame.pos.left = { relative, relativeSide, xOffset: x, yOffset: 0 };
      }

      if (rightPoints.includes(point)) {
        frame.pos.right = { relative, relativeSide, xOffset: x, yOffset: 0 };
      }

      if (topPoints.includes(point)) {
        frame.pos.top = { relative, relativeSide, xOffset: 0, yOffset: y };
      }

      if (bottomPoints.includes(point)) {
        frame.pos.bottom = { relative, relativeSide, xOffset: 0, yOffset: y };
      }

      if (point === FRAMEPOINT_CENTER) {
        frame.pos.center = { relative, relativeSide, xOffset: x, yOffset: y };
      }

      adapter.update(frame);
    };

    this.BlzFrameSetAbsPoint = (
      frame: framehandle,
      point: framepointtype,
      x: number,
      y: number,
    ): void => {
      if (leftPoints.includes(point)) frame.pos.left = x;
      if (rightPoints.includes(point)) frame.pos.right = x;
      if (topPoints.includes(point)) frame.pos.top = y;
      if (bottomPoints.includes(point)) frame.pos.bottom = y;

      if (point === FRAMEPOINT_CENTER) frame.pos.center = { x, y };

      adapter.update(frame);
    };

    this.BlzFrameClearAllPoints = (frame: framehandle): void => {
      frame.pos = {
        left: undefined,
        right: undefined,
        bottom: undefined,
        top: undefined,
        center: undefined,
      };
    };

    this.BlzFrameSetAllPoints = (
      frame: framehandle,
      relative: framehandle,
    ): void => {
      frame.pos = {
        top: { relative, relativeSide: "top", xOffset: 0, yOffset: 0 },
        bottom: { relative, relativeSide: "bottom", xOffset: 0, yOffset: 0 },
        left: { relative, relativeSide: "left", xOffset: 0, yOffset: 0 },
        right: { relative, relativeSide: "right", xOffset: 0, yOffset: 0 },
        center: undefined,
      };
      adapter.update(frame);
    };

    this.BlzFrameSetVisible = (
      frame: framehandle | null,
      visible: boolean,
    ): void => {
      if (!frame) return;
      frame.visible = visible;
      adapter.update(frame);
    };

    this.BlzFrameIsVisible = (frame: framehandle): boolean => {
      console.log("Declared BlzFrameIsVisible");
      return false;
    };

    this.BlzGetFrameByName = (
      name: string,
      createContext: number,
    ): framehandle => adapter.selectAll(`[name=${name}]`)[createContext];

    this.BlzFrameGetName = (frame: framehandle): string => {
      console.log("Declared BlzFrameGetName");
      return "";
    };

    this.BlzFrameClick = (frame: framehandle): void => { };

    this.BlzFrameSetText = (frame: framehandle, text: string): void => {
      frame.text = text;
      adapter.update(frame);
    };

    this.BlzFrameGetText = (frame: framehandle): string | undefined =>
      frame.text;

    this.BlzFrameAddText = (frame: framehandle, text: string): void => {
      frame.text = (frame.text ?? "") + text;
      adapter.update(frame);
    };

    this.BlzFrameSetTextSizeLimit = (
      frame: framehandle,
      size: number,
    ): void => { };

    this.BlzFrameGetTextSizeLimit = (frame: framehandle): number => {
      console.log("Declared BlzFrameGetTextSizeLimit");
      return 0;
    };

    this.BlzFrameSetTextColor = (
      frame: framehandle,
      color: number,
    ): void => { };

    this.BlzFrameSetFocus = (frame: framehandle, flag: boolean): void => { };

    this.BlzFrameSetModel = (
      frame: framehandle,
      modelFile: string,
      cameraIndex: number,
    ): void => { };

    this.BlzFrameSetEnable = (
      frame: framehandle,
      enabled: boolean,
    ): void => { };

    this.BlzFrameGetEnable = (frame: framehandle): boolean => {
      console.log("Declared BlzFrameGetEnable");
      return false;
    };

    this.BlzFrameSetAlpha = (frame: framehandle, alpha: number): void => {
      frame.alpha = alpha;
      adapter.update(frame);
    };

    this.BlzFrameGetAlpha = (frame: framehandle): number =>
      frame.alpha ?? 255;

    this.BlzFrameSetSpriteAnimate = (
      frame: framehandle,
      primaryProp: number,
      flags: number,
    ): void => { };

    this.BlzFrameSetTexture = (
      frame: framehandle,
      texFile: string,
      flag: number,
      blend: boolean,
    ): void => {
      frame.image = texFile;
      adapter.update(frame);
    };

    this.BlzFrameSetScale = (frame: framehandle, scale: number): void => {
      frame.scale = scale;
      adapter.update(frame);
    };

    this.BlzFrameSetTooltip = (
      frame: framehandle,
      tooltip: framehandle,
    ): void => {
      adapter.setTooltip(this, frame, tooltip);
    };

    this.BlzFrameCageMouse = (
      frame: framehandle,
      enable: boolean,
    ): void => { };

    this.BlzFrameSetValue = (frame: framehandle, value: number): void => { };

    this.BlzFrameGetValue = (frame: framehandle): number => {
      console.log("Declared BlzFrameGetValue");
      return 0;
    };

    this.BlzFrameSetMinMaxValue = (
      frame: framehandle,
      minValue: number,
      maxValue: number,
    ): void => { };

    this.BlzFrameSetStepSize = (
      frame: framehandle,
      stepSize: number,
    ): void => { };

    this.BlzFrameSetSize = (
      frame: framehandle,
      width: number,
      height: number,
    ): void => {
      frame.width = width;
      frame.height = height;
      adapter.update(frame);
    };

    this.BlzFrameSetVertexColor = (
      frame: framehandle,
      color: number,
    ): void => { };

    this.BlzFrameSetLevel = (frame: framehandle, level: number): void => { };

    this.BlzFrameSetParent = (
      frame: framehandle,
      parent: framehandle,
    ): void => { };

    this.BlzFrameGetParent = (frame: framehandle): framehandle | null =>
      frame.parent;

    this.BlzFrameGetHeight = (frame: framehandle): number => {
      console.log("Declared BlzFrameGetHeight");
      return 0;
    };

    this.BlzFrameGetWidth = (frame: framehandle): number => {
      console.log("Declared BlzFrameGetWidth");
      return 0;
    };

    this.BlzFrameSetFont = (
      frame: framehandle,
      fileName: string,
      height: number,
      flags: number,
    ): void => { };

    this.BlzFrameSetTextAlignment = (
      frame: framehandle,
      vert: textaligntype,
      horz: textaligntype,
    ): void => { };

    this.BlzTriggerRegisterFrameEvent = (
      whichTrigger: trigger,
      frame: framehandle,
      eventId: frameeventtype,
    ): event => {
      const event = <event>{ handleId: eventId.handleId, eventId, type: eventId, agentId: whichTrigger.agentId };
      whichTrigger.events.push(event);

      adapter.addFrameListener(frame, eventId, () => {
        if (whichTrigger.evaluate()) whichTrigger.execute();
      });

      return event;
    };

    this.BlzGetTriggerFrame = (): framehandle => {
      console.log("Declared BlzGetTriggerFrame");
      return (null as unknown) as framehandle;
    };

    this.BlzGetTriggerFrameEvent = (): frameeventtype => {
      console.log("Declared BlzGetTriggerFrameEvent");
      return (null as unknown) as frameeventtype;
    };

    this.BlzGetTriggerFrameValue = (): number => {
      console.log("Declared BlzGetTriggerFrameValue");
      return 0;
    };

    this.BlzGetTriggerFrameText = (): string => {
      console.log("Declared BlzGetTriggerFrameText");
      return "";
    };

    this.BlzFrameGetChildrenCount = (frame: framehandle): number =>
      frame.children.length;

    this.BlzFrameGetChild = (
      frame: framehandle,
      index: number,
    ): framehandle => frame.children[index];
  }
}


const leftPoints = [FRAMEPOINT_TOPLEFT, FRAMEPOINT_LEFT, FRAMEPOINT_BOTTOMLEFT];
const rightPoints = [
  FRAMEPOINT_TOPRIGHT,
  FRAMEPOINT_RIGHT,
  FRAMEPOINT_BOTTOMRIGHT,
];
const topPoints = [FRAMEPOINT_TOPRIGHT, FRAMEPOINT_TOP, FRAMEPOINT_TOPLEFT];
const bottomPoints = [
  FRAMEPOINT_BOTTOMRIGHT,
  FRAMEPOINT_BOTTOM,
  FRAMEPOINT_BOTTOMLEFT,
];
const pointMap = new Map<framepointtype, RelativeFrameSide["relativeSide"]>([
  [FRAMEPOINT_TOPLEFT, "topleft"],
  [FRAMEPOINT_TOP, "top"],
  [FRAMEPOINT_TOPRIGHT, "topright"],
  [FRAMEPOINT_LEFT, "left"],
  [FRAMEPOINT_CENTER, "center"],
  [FRAMEPOINT_RIGHT, "right"],
  [FRAMEPOINT_BOTTOMLEFT, "bottomleft"],
  [FRAMEPOINT_BOTTOM, "bottom"],
  [FRAMEPOINT_BOTTOMRIGHT, "bottomright"],
]);
