
import { Adapter } from "./adapter";
import { FRAMEPOINT_BOTTOMLEFT, ORIGIN_FRAME_GAME_UI, framehandle } from "./customUiApi";
import { FrameLib } from "./frames";


export const initUI = (frameDefs) => {
  let adapter = new Adapter();
  let frameLib = new FrameLib(frameDefs, adapter);
  const gameUI = frameLib.BlzCreateFrame(
    "Loading",
    null as unknown as framehandle,
    0,
    0,
  );
  frameLib.BlzFrameSetAllPoints(gameUI,null);
  // adapter.markOrigin(gameUI, ORIGIN_FRAME_GAME_UI);
  frameLib.BlzFrameSetAbsPoint(gameUI, FRAMEPOINT_BOTTOMLEFT, 0, 0);
  frameLib.BlzFrameSetSize(gameUI, 0.8, 0.6);

  // const consoleUI = frameLib.BlzCreateFrame("ConsoleUI", gameUI, 0, 0);
  // frameLib.BlzCreateFrame("idk", consoleUI, 0, 0);
  // frameLib.BlzCreateFrame("idk", consoleUI, 0, 1);
  // frameLib.BlzCreateFrame("idk", consoleUI, 0, 2);
  // frameLib.BlzCreateFrame("idk", consoleUI, 0, 3);
  // frameLib.BlzCreateFrame("idk", consoleUI, 0, 4);
  // frameLib.BlzCreateFrame("idk", consoleUI, 0, 5);
}
