export type RelativeFrameSide = {
  relative: framehandle;
  relativeSide:
  | "topleft"
  | "top"
  | "topright"
  | "left"
  | "center"
  | "right"
  | "bottomleft"
  | "bottom"
  | "bottomright";
  xOffset: number;
  yOffset: number;
};
export type FrameSide = number | undefined | RelativeFrameSide;
