export class ArrayList {
    d = [];
    constructor(n = 0, transform = (x) => x) {
        this.d = new Array(n);
        this.transform = transform;
    }
    fill(ctor) {
        this.d.fill(this.transform(new ctor()));
    }
    getAt(index, factory) {
        if (index instanceof Array) {
            // enums: [enumType,enumName,enumInt]
            let i = index[2];
            if (this.d[i] == undefined) {
                this.d[i] = this.transform(factory(i));
            }
            let item = this.d[i];
            item[index[0]] = index[1];
            return item;
        }
        else {
            if (this.d[index] == undefined) {
                this.d[index] = this.transform(factory(index));
            }
            return this.d[index];
        }
    }
    get(index) {
        if (index instanceof Array) {
            // enums: [enumType,enumName,enumInt]
            let i = index[2];
            let item = this.d[i];
            item[index[0]] = index[1];
            return item;
        }
        else
            return this.d[index];
    }
    put(index, v) {
        this.d[index] = this.transform(v);
        return v;
    }
    remove(item) {
        let index = this.d.indexOf(item);
        if (index != -1)
            this.d.splice(index, 1);
        else
            console.log('E:removing none exist item', item);
    }
    add(item) {
        this.d.push(this.transform(item));
    }
    contains(item) {
        return this.d.includes(item);
    }
    has(item) {
        return this.d.includes(item);
    }
    set(index, item) {
        this.d[index] = this.transform(item);
    }
    clear() {
        this.d.length = 0;
    }
    forEach(fn) {
        this.d.forEach((x, i) => fn(x, i, this.d));
    }
    filter(fn) {
        return this.d.filter((x, i) => fn(x, i, this.d));
    }
    replace(d = []) {
        this.d = this.transform(d);
    }
    any(fn) {
        for (let i = 0; i < this.d.length; i++) {
            const element = this.d[i];
            if (fn(element, i, this.d)) {
                return true;
            }
        }
        return false;
    }
    size() {
        return this.d.length;
    }
}
class timer {
    handle = 0;
    periodic = false;
    timeout = -1;
    handlerFunc;
    timerExpire = false;
    triggers = new ArrayList();
    callT = 0;
    beginT = 0;
    resumeDelay = 0;
    start(timeoutSeconds, periodic, handlerFunc) {
        if (this.handle && this.periodic)
            throw new Error('Timer already started');
        this.callT = performance.now();
        this.beginT = performance.now();
        this.periodic = periodic;
        this.timeout = timeoutSeconds * 1000;
        this.handlerFunc = handlerFunc;
        console.log('timer start:', this);
        this.handle = setTimeout(() => this.handler(), this.timeout);
    }
    async handler() {
        this.callT = performance.now();
        if (this.handlerFunc) {
            console.log('timer callback:', this.handlerFunc);
            let p = this.handlerFunc?.call(null);
            if (p instanceof Promise) {
                await p;
            }
        }
        this.triggers.forEach(x => x.execute());
        if (this.periodic) {
            this.handle = setTimeout(() => this.handler(), this.timeout);
        }
    }
    listenExpire(trigger) {
        this.triggers.add(trigger);
    }
    destroy() {
        console.log('timer destroy:', this);
        if (this.periodic) {
            clearInterval(this.handle);
        }
        else
            clearTimeout(this.handle);
        this.handle = 0;
    }
    getElapsed() {
        return performance.now() - this.callT;
    }
    pause() {
        clearTimeout(this.handle);
        this.handle = 0;
        let elapsed = performance.now() - this.callT;
        let remaining = this.timeout - elapsed;
        this.resumeDelay = remaining;
    }
    resume() {
        this.handle = setTimeout(this.handler, this.resumeDelay);
    }
}
