class trigger {
    gameStateEvents = new ArrayList();
    gameEvents = new ArrayList();
    unitEvents = new ArrayList();
    playerUnitEvents = new ArrayList();
    timerExpireEvents = new ArrayList();
    VariableEvents = new ArrayList();
    actions = new ArrayList();
    evaluate(stateRef) {
        if (0 == this.gameStateEvents.size()
            + this.unitEvents.size()
            + this.playerUnitEvents.size()
            + this.timerExpireEvents.size()
            + this.gameEvents.size()
        ) {
            return true;
        }
        return this.gameStateEvents.any(x => x.evaluate(stateRef))
            || this.unitEvents.any(x => x.evaluate(stateRef))
            || this.playerUnitEvents.any(x => x.evaluate(stateRef))
            || this.playerUnitEvents.any(x => x.evaluate(stateRef));
    }
    execute() {
        console.log('trigger execute actions', this.actions)
        this.actions.forEach(x => x());
    }
}
