class propWatcher {
    constructor(target) {
        this.watchTarget = target;
    }
    listeners = new ArrayList();
    set(target, prop, value, reciver) {
        if (target[prop] && target[prop] != value) {
            Reflect.set(target, prop, value, reciver);
            let relatedListeners = this.listeners.filter(x => x.whichPropName == prop);
            relatedListeners.forEach(x => x.filter({ prop, value }) && x.execute());
        }
        return true;
    }
    get(target, prop, receiver) {
        if (prop == 'eventListeners') {
            return this.listeners;
        }
        return Reflect.get(target, prop, receiver);
    }
}

const asEventAgent = (obj) => {
    return new Proxy(obj, new propWatcher(obj));
}

const listenEvent = (whichListenTarget, whichPropName, trigger, filter) => {
    if (!whichListenTarget || !whichListenTarget.eventListeners) {
        debugger;
        console.error("invalid listenTarget", whichListenTarget);
    }
    whichListenTarget.eventListeners.add({
        filter: filter || (() => true),
        execute: trigger.execute,
        whichPropName
    })
}
