    ForceEnumPlayers(whichForce, filter) {
        if (filter == null) {
            whichForce.players.replace(this.players.d);
        }
        else {
            whichForce.players.replace(this.players.filter(x => filter.func()));
        }
    }
    /* native_decl */
    ForceEnumPlayersCounted(whichForce, filter, countLimit) {
        debugger;
        //todo iterator
        let players = this.players.filter(filter.func);
        if (players.length > countLimit) {
            players.length = countLimit;
        }
        whichForce.players.replace(players);
    }
    /* native_decl */
    ForceEnumAllies(whichForce, whichPlayer, filter) {
        debugger;
        let allies = [];
        let players = whichPlayer.AlliancePlayers.filter(x => filter.func())
        whichForce.AlliancePlayers.replace(players);
    }
