    TriggerRegisterVariableEvent(whichTrigger, varName, opcode, limitval) {
        let filter = new eventFilter(varName, opcode, limitval);
        listenEvent(this.runtime, varName, (p) => filter.evaluate(p))
        whichTrigger.VariableEvents.add(new {
            varName, opcode, limitval
        })
    }
    /* native_decl */
    TriggerRegisterTimerEvent(whichTrigger, timeout, periodic) {
        let t = timerListen(timeout, periodic, whichTrigger);
        whichTrigger.TimerEvents.add({
            timeout,
            periodic,
            timer: t
        });
    }
    /* native_decl */
    TriggerRegisterTimerExpireEvent(whichTrigger, t) {
        t.listenExpire(whichTrigger);
        whichTrigger.timerExpireEvents.add(new timerExpireEvent(t));
    }
