    TriggerRegisterGameStateEvent(whichTrigger, whichState, opcode, limitval) {
        let filter = new eventFilter(whichState, opcode, limitval);
        listenEvent(this.gameState, whichState, whichTrigger, (p) => filter.evaluate(p));
        whichTrigger.gameStateEvents.add(new eventFilter(whichState, opcode, limitval));
    }
