    AddItemToAllStock(itemId, currentStock, stockMax) {
        this.NbShops.forEach(x => x.items.set(itemId, { currentStock, stockMax }));
    }
    /* native_decl */
    AddItemToStock(whichUnit, itemId, currentStock, stockMax) {
        whichUnit.units.set(itemId, { currentStock, stockMax })
    }
    /* native_decl */
    AddUnitToAllStock(unitId, currentStock, stockMax) {
        this.NbShops.forEach(x => x.units.set(unitId, { currentStock, stockMax }));
    }
    /* native_decl */
    AddUnitToStock(whichUnit, unitId, currentStock, stockMax) {
        whichUnit.units.set(unitId, { currentStock, stockMax })
    }
    /* native_decl */
    RemoveItemFromAllStock(itemId) {
        this.NbShops.forEach(x => x.items.remove(itemId));
    }
    /* native_decl */
    RemoveItemFromStock(whichUnit, itemId) {
        whichUnit.items.remove(itemId);
    }
    /* native_decl */
    RemoveUnitFromAllStock(unitId) {
        this.NbShops.forEach(x => x.units.remove(unitId));
    }
    /* native_decl */
    RemoveUnitFromStock(whichUnit, unitId) {
        whichUnit.units.remove(unitId);
    }
    /* Neutral Buildings */
    SetAllItemTypeSlots(slots) {
        this.NbShops.forEach(x => x.items = new ArrayList(slots));
    }
