    CreateTimerDialog(t) {
        let d = {
            type: 'timerdialog',
            creationArgs: [...arguments],
            execute: () => {
                alert('timerDialog');
            }
        }
        if (t) {
            t.listenExpire(d)
        }
        return d;
    }
    /* native_decl */
    DestroyTimerDialog(whichDialog) {
        // do nothing.
    }
    /* native_decl */
    TimerDialogSetTitle(whichDialog, title) {
        whichDialog.title = title;
    }
    /* native_decl */
    TimerDialogSetTitleColor(whichDialog, red, green, blue, alpha) {
        whichDialog.titleColor = [red, green, blue, alpha, 'rgba']
    }
    /* native_decl */
    TimerDialogSetTimeColor(whichDialog, red, green, blue, alpha) {
        whichDialog.timeColor = [red, green, blue, alpha, 'rgba']
    }
    /* native_decl */
    TimerDialogSetSpeed(whichDialog, speedMultFactor) {
        whichDialog.speedMultFactor = speedMultFactor;
    }
    /* native_decl */
    TimerDialogDisplay(whichDialog, display) {
        this.app.showTimerDialog(whichDialog, display);
    }
    /* native_decl */
    IsTimerDialogDisplayed(whichDialog) {
        return whichDialog.displayed;
    }
    /* native_decl */
    TimerDialogSetRealTimeRemaining(whichDialog, timeRemaining) {
        whichDialog.timeRemaining = timeRemaining;
    }
