/* eslint-disable */
const { CstParser, EMPTY_ALT, EOF } = require('chevrotain');
const { JassLexer, tokenVocabulary: jassTokens } = require('./JassLexer');
for (const key in jassTokens) {
  if (Object.hasOwnProperty.call(jassTokens, key)) {
    global[key] = jassTokens[key];
  }
}
/* eslint-disable no-undef */  

class JassParser extends CstParser {
  constructor() {
    super(jassTokens);
    const $ = this;

    $.RULE('program', () => {
      $.MANY(() => {
        $.OR([
          { ALT: () => $.SUBRULE($.typeDefinition) },
          { ALT: () => $.SUBRULE($.nativeBlock) },
          { ALT: () => $.SUBRULE($.globalsBlock) },
          { ALT: () => $.SUBRULE($.functionBlock) },
        ]);
      })
    });
    $.RULE('typeDefinition', () => {
      $.CONSUME(TYPE);
      $.CONSUME1(ID);
      $.CONSUME2(EXTENDS);
      $.CONSUME3(ID);
    });

    $.RULE('type', () => {
      $.OR([
        { ALT: () => $.CONSUME(NOTHING) },
        {
          ALT: () => {
            $.CONSUME(ID);
            $.OPTION(() => $.CONSUME(ARRAY));
          }
        }
      ])
    });
    $.RULE('globalVar', () => {
      $.OPTION(() => $.CONSUME(CONSTANT), { LABEL: 'constant' });
      // typeName
      $.SUBRULE($.type, { LABEL: 'type' });
      // identifier
      $.CONSUME2(ID, { LABEL: 'label' });
      $.OPTION3(() => {
        $.CONSUME(EQUALS);
        $.SUBRULE($.expression)
      });
    });
    $.RULE('localVars', () => {
      $.CONSUME1(LOCAL);
      // typeName
      $.SUBRULE($.type, { LABEL: 'type' });
      // identifier
      $.CONSUME2(ID, { LABEL: 'label' });
      $.OPTION3(() => {
        $.CONSUME(EQUALS);
        $.SUBRULE($.expression)
      });
    });

    $.RULE('expression', () => {
      const left = { LABEL: 'left' }
      const op = { LABEL: 'op' }
      const right = { LABEL: 'right' }
      $.OR([
        { ALT: () => $.SUBRULE($.ParentheticalExpression, left) },
        { ALT: () => $.SUBRULE($.NotExpression, left) },
        { ALT: () => $.SUBRULE($.NegateExpression, left) },
        { ALT: () => $.CONSUME(IDLITERAL, left) },
        { ALT: () => $.CONSUME(STRINGLITERAL, left) },
        { ALT: () => $.CONSUME(HEX_CONSTANT, left) },
        { ALT: () => $.CONSUME(DOLLAR_HEX_CONSTANT, left) },
        { ALT: () => $.CONSUME(RAWCODE, left) },
        { ALT: () => $.SUBRULE($.jassValueOf, left) },
        { ALT: () => $.SUBRULE($.FunctionReferenceExpression, left) },
        { ALT: () => $.CONSUME(INTEGER, left) },
        { ALT: () => $.CONSUME(NULL, left) },
        { ALT: () => $.CONSUME(FALSE, left) },
        { ALT: () => $.CONSUME(TRUE, left) },
        { ALT: () => $.CONSUME(REAL, left) },
      ]);
      $.OPTION(() => {
        $.OR2([
          { ALT: () => $.CONSUME(AND, op) },
          { ALT: () => $.CONSUME(OR, op) },
          { ALT: () => $.CONSUME(EQUALSEQUALS, op) },
          { ALT: () => $.CONSUME(LESSOREQUALS, op) },
          { ALT: () => $.CONSUME(GREATEROREQUALS, op) },
          { ALT: () => $.CONSUME(LESS, op) },
          { ALT: () => $.CONSUME(GREATER, op) },
          { ALT: () => $.CONSUME(NOTEQUALS, op) },
          { ALT: () => $.CONSUME(PLUS, op) },
          { ALT: () => $.CONSUME(MINUS, op) },
          { ALT: () => $.CONSUME(MULT, op) },
          { ALT: () => $.CONSUME(DIV, op) },
        ]);
        $.SUBRULE2($.expression, right)
      })
    });

    $.RULE('StringLiteralExpression', () => {
      $.CONSUME(STRINGLITERAL)
    });
    $.RULE('HexIntegerLiteralExpression', () => {
      $.CONSUME(HEX_CONSTANT)
    });
    $.RULE('DollarHexIntegerLiteralExpression', () => {
      $.CONSUME(DOLLAR_HEX_CONSTANT)
    });

    $.RULE('FunctionReferenceExpression', () => {
      $.CONSUME1(FUNCTION)
      $.CONSUME2(ID)
    });

    $.RULE('jassValueOf', () => {
      const d = $.CONSUME(ID)
      $.OPTION(() => {
        $.OR([
          { ALT: () => $.SUBRULE($.arrayAccess) },
          {
            ALT: () => {
              $.CONSUME2(LPAREN)
              $.SUBRULE($.argList)
              $.CONSUME3(RPAREN)
            }
          },
          {
            ALT: () => {
              $.CONSUME(LPAREN)
              $.CONSUME(RPAREN)
            }
          }
        ]);
      })
    });

    $.RULE('functionCallExpression', () => {
      let x1 = $.CONSUME1(ID)
      let x2 = $.CONSUME2(LPAREN)
      let x3 = $.OPTION(() => $.SUBRULE($.argList));
      let x4 = $.CONSUME3(RPAREN)
    });


    $.RULE('arrayAccess', () => {
      $.CONSUME2(LSQUAREPAREN)
      $.SUBRULE($.expression)
      $.CONSUME3(RSQUAREPAREN)
    });

    $.RULE('ParentheticalExpression', () => {
      $.CONSUME(LPAREN)
      const d = $.SUBRULE($.expression)
      $.CONSUME(RPAREN)
    });

    $.RULE('NotExpression', () => {
      $.CONSUME(NOT)
      $.SUBRULE($.expression)
    });

    $.RULE('NegateExpression', () => {
      $.CONSUME(MINUS)
      $.SUBRULE($.expression)
    });


    $.RULE('argList', () => {
      const d = $.SUBRULE($.expression)
      $.MANY({
        DEF: () => {
          const comma = $.CONSUME(COMMA);
          $.SUBRULE2($.expression);
        }
      })
      // $.OPTION(() => {
      //   $.AT_LEAST_ONE_SEP({
      //     SEP: COMMA,
      //     DEF: () => {
      //       const d = $.SUBRULE2($.expression);
      //     }
      //   })
      // })
    });

    $.RULE('statement', () => {
      const label = { LABEL: 'statements' };
      $.OR([
        { ALT: () => $.SUBRULE($.callStatement, label) },
        { ALT: () => $.SUBRULE($.setStatement, label) },
        { ALT: () => $.SUBRULE($.returnStatement, label) },
        { ALT: () => $.SUBRULE($.exitWhenStatement, label) },
        { ALT: () => $.SUBRULE($.localVars, label) },
        { ALT: () => $.SUBRULE($.loopStatement, label) },
        { ALT: () => $.SUBRULE($.ifStatement, label) },
        // ALT: ()=>$.SUBRULE($.DebugStatement)
      ]);
    });
    $.RULE('callStatement', () => {
      $.CONSUME(CALL)
      $.SUBRULE($.functionCallExpression)
    });


    $.RULE('setStatement', () => {
      $.CONSUME1(SET)
      $.CONSUME2(ID)
      $.OPTION(() => $.SUBRULE($.arrayAccess))
      $.CONSUME3(EQUALS)
      $.SUBRULE($.expression)
    });

    $.RULE('returnStatement', () => {
      $.CONSUME(RETURN)
      $.OPTION(() => $.SUBRULE($.expression))
    });

    $.RULE('exitWhenStatement', () => {
      $.CONSUME(EXITWHEN)
      $.SUBRULE($.expression)
    });

    $.RULE('loopStatement', () => {
      $.CONSUME(LOOP)
      $.MANY(() => $.SUBRULE($.statement));
      $.CONSUME(ENDLOOP)
    });
    $.RULE('ifStatement', () => {
      $.CONSUME(IF)
      $.SUBRULE($.expression, { LABEL: 'condition' })
      $.CONSUME(THEN)
      $.MANY(() => $.SUBRULE($.statement, { LABEL: 'statements' }));
      $.MANY2(() => $.SUBRULE($.elseIfStatement, { LABEL: 'elseIfStatement' }));
      $.MANY3(() => $.SUBRULE($.elseStatement, { LABEL: 'elseStatement' }));
      $.CONSUME4(ENDIF)
    });
    $.RULE('elseIfStatement', () => {
      $.CONSUME(ELSEIF)
      $.SUBRULE($.expression, { LABEL: 'condition' })
      $.CONSUME(THEN)
      $.MANY(() => $.SUBRULE($.statement, { LABEL: 'statements' }));
    });

    $.RULE('elseStatement', () => {
      $.CONSUME(ELSE)
      $.MANY(() => $.SUBRULE($.statement, { LABEL: 'statements' }));
    });

    $.RULE('param', () => {
      $.OR([
        {
          ALT: () => {
            $.SUBRULE($.type)
            $.CONSUME(ID)
          }
        },
        { ALT: () => $.CONSUME(NOTHING) },
      ])
    });
    $.RULE('paramList', () => {
      const d = $.SUBRULE($.param)
      $.MANY({
        DEF: () => {
          $.CONSUME(COMMA);
          $.SUBRULE2($.param);
        }
      })
    });
    $.RULE('globalsBlock', () => {
      $.CONSUME(GLOBALS);
      $.MANY(() => $.SUBRULE($.globalVar, { LABEL: 'globalVars' }));
      $.CONSUME(ENDGLOBALS);
    });

    $.RULE('nativeBlock', () => {
      $.OPTION(() => $.CONSUME(CONSTANT));
      $.CONSUME(NATIVE);
      $.CONSUME(ID);
      $.CONSUME(TAKES);
      $.SUBRULE($.paramList);
      $.CONSUME(RETURNS);
      $.SUBRULE($.type, { LABEL: 'returnType' });
    });



    $.RULE('functionBlock', () => {
      let a = $.OPTION(() => $.CONSUME(CONSTANT));
      let b = $.CONSUME(FUNCTION);
      let c = $.CONSUME(ID, { LABEL: 'functionName' });
      let d = $.CONSUME(TAKES);
      let e = $.SUBRULE($.paramList);
      let f = $.CONSUME(RETURNS);
      let j = $.SUBRULE($.type, { LABEL: 'returnType' });
      let h = $.MANY(() => $.SUBRULE($.statement, 'statements'));
      let i = $.CONSUME(ENDFUNCTION);
    });
    this.performSelfAnalysis()
  }
}

class visitor {
  visit(d, depth) {
    if (d instanceof Array) {
      this.visitArray(d, depth);
    }
    else {
      this.visitObject(d, depth);
    }
  }
  visitObject(d, depth) {
    if (!Object.keys(d).length) {
      return;
    }

    if (d.image) {
      console.log(this.indent(depth), '#' + d.tokenType.name, '\'' + d.image + "\'", ', end at line', d.endLine + ':' + d.endColumn);
      return;
    }
    if (d.name) {
      console.log(this.indent(depth), '#' + d.name);
    }
    for (const key in d) {
      const element = d[key];
      if (key == 'name') {
        continue;
      }
      if (Object.hasOwnProperty.call(d, key)) {
        if (element instanceof Array) {
          this.visitArray(element, depth + 1);
        }
        else if (element.__proto__ && element.__proto__.constructor.name == 'String') {
          console.log(this.indent(depth) + key, '=', '\'' + element + '\'');
        }
        // else if (element.__proto__ && element.__proto__.constructor.name == 'Number') {
        //   console.log(this.indent(depth) + key, '=', element);
        // }
        else if (Object.keys(element).length) {
          this.visitObject(element, depth + 1);
        }
        else {
          console.log(this.indent(depth), '>', 'no-elements-found')
        }
      }
    }
  }
  visitArray(d, depth) {
    if (d instanceof Array) {
      for (let i = 0; i < d.length; i++) {
        const element = d[i];
        this.visit(element, depth);
      }
    }
  }
  indent(depth) {
    return new Array(depth * 2).fill(' ').join('');
  }
}
module.exports = {
  JassParser,
  parse: function (inputText, rule, debug) {
    const parserInstance = new JassParser();
    const lexResult = JassLexer.tokenize(inputText);
    parserInstance.input = lexResult.tokens;
    parserInstance.debug = (rule && rule != 'program') || debug;
    parserInstance.constVisitor = new visitor();
    parserInstance.cstPostRule = function (d) {
      if (this.debug)
        this.constVisitor.visit(d, 0);
    }
    console.log('==== start parser program === ')
    const output = parserInstance[rule || 'program']();
    if (parserInstance.errors.length > 0) {
      throw new Error(parserInstance.errors[0].message);
    }
    return output;
  },
};